<template>
    <div align="center">
        
        <b-alert style="max-width: 80rem;" show variant="warning"><strong>퍼센트 계산기</strong></b-alert>

        <b-card
            border-variant="warning"
            header=""
            header-bg-variant="transparent"
            align="center"
            style="max-width: 80rem;"
        >

            <div class="mb-4 w-full md:mb-0 flatpickr-input">
                <input type="number" v-model="persentVar1" placeholder="전체값 예)100"> 의
                &nbsp;
                <input type="number" v-model="persentVar2" placeholder="(비율값) 예)20"> %는 얼마일까?
            </div>

            <div>
                <h4>
                    <b-badge variant="light">전체 </b-badge>
                    <b-badge variant="primary">{{persentVar1}} </b-badge>
                    <b-badge variant="light">의 </b-badge>
                    <b-badge variant="info">{{persentVar2}}%</b-badge>
                    <b-badge variant="light">는 </b-badge>
                    <b-badge variant="danger">{{calculate}}</b-badge>
                    <b-badge variant="light">입니다 </b-badge>
                </h4>
            </div>
        </b-card>
<br>
        <b-card
            border-variant="warning"
            header=""
            header-bg-variant="transparent"
            align="center"
            style="max-width: 80rem;"
        >

            <div class="mb-4 w-full md:mb-0 flatpickr-input">
                <input type="number" v-model="persentVar3" placeholder="전체값 예)100"> 의
                &nbsp;
                <input type="number" v-model="persentVar4" placeholder="(일부값) 예)50">는 몇 % 일까?
            </div>

            <div>
                <h4>
                    <b-badge variant="light">전체 </b-badge>
                    <b-badge variant="primary">{{persentVar3}} </b-badge>
                    <b-badge variant="light">의 </b-badge>
                    <b-badge variant="info">{{persentVar4}}</b-badge>
                    <b-badge variant="light">은 </b-badge>
                    <b-badge variant="danger">{{calculate2}}%</b-badge>
                    <b-badge variant="light">입니다 </b-badge>
                </h4>
            </div>
        </b-card>

    </div>







