</template>

<script>
export default {

    data() {
        return{
            persentVar1: '',
            persentVar2: '',
            persentVar3: '',
            persentVar4: '',
            persentVar5: '',
            persentVar6: '',
        };
    },

    computed:
    {
        calculate(){
            return this.persentVar1 * this.persentVar2/100
        },
        calculate2(){
            return this.persentVar4 / this.persentVar3 * 100
        },
        calculate3(){
            return this.persentVar6 / this.persentVar5 * 100
        }
    }

}
</script>

